// src/lib/websocket-client.js
import { writable, get } from 'svelte/store';
import { user } from '$lib/stores';

export const status = writable(' Connecting...');
export const users = writable([]);
export const messages = writable([]);
export const hasMore = writable(true);

export const myId = writable(null);
export const myName = writable('');

//  Listen to user store changes
user.subscribe(u => {
    if (u) {
        myId.set(u.id);
        myName.set(u.name);
    } else {
        myId.set(null);
        myName.set('');
    }
});

const WS_URL = import.meta.env.VITE_WS_URL;

let socket = null;
let reconnectTimer = null;
/* ========================================================
    CONNECT WEBSOCKET  WITH COOKIE (refreshToken)
   Backend s c cookie refreshToken  login WS
======================================================== */
export function connectWebSocket() {
    if (socket && socket.readyState !== WebSocket.CLOSED) {
        console.log(" WS already connected or connecting");
        return socket;
    }

    //console.log(` Connecting WebSocket  ${WS_URL}`);

    //  QUAN TRNG: WebSocket phi bt ch  gi cookie
    socket = new WebSocket(WS_URL);

    socket.onopen = () => {
        //console.log(" WS connected");
        status.set(" Connected");
    };


    /* ========================================================
        RECEIVE MESSAGE
    ======================================================== */
    socket.onmessage = (event) => {
        try {
            const data = JSON.parse(event.data);

            // Danh sch user online
            if (data.type === "users") {
                users.set(data.users);
                return;
            }

            // Welcome message
            if (data.type === "welcome") {
                messages.update(m => [...m, data]);
                return;
            }

            // Chat message
            if (data.type === "chat") {
                messages.update(m => [...m, data]);
                return;
            }

        } catch (err) {
            console.warn(" Invalid WS message:", event.data);
        }
    };

    /* ========================================================
        AUTO RECONNECT
    ======================================================== */
    socket.onclose = () => {
        console.log(" WS disconnected  retry in 2s...");
        status.set(" Disconnected  retrying...");

        clearTimeout(reconnectTimer);
        reconnectTimer = setTimeout(connectWebSocket, 2000);
    };

    return socket;
}

/* ========================================================
    SEND MESSAGE (NO accessToken)
======================================================== */
export function sendMessage(data) {
    if (!socket || socket.readyState !== WebSocket.OPEN) {
        console.warn(" WS not ready");
        return;
    }
    console.log('send:', data)
    socket.send(JSON.stringify(data));
}

/* ========================================================
    BINARY SUPPORT
======================================================== */
export const sendChunk = (buffer) =>
    socket?.readyState === WebSocket.OPEN && socket.send(buffer);

export const sendStop = () =>
    socket?.readyState === WebSocket.OPEN && socket.send("stop");

/* ========================================================
    CLOSE MANUALLY
======================================================== */
export function closeSocket() {
    if (socket) {
        console.log(" Closing WS...");
        socket.close();
        socket = null;
    }
}

/* ========================================================
    LOAD MESSAGE HISTORY (Backend)
======================================================== */
export async function loadMessages(before = null) {
    try {
        const u = get(user);
        if (!u) return;

        let url = `${import.meta.env.VITE_API_BASE_URL}/messages?userId=${u.id}`;
        if (before) url += `&before=${before}`;

        const res = await fetch(url, {
            headers: {
                // Backend /messages cn accessToken
                Authorization: `Bearer ${localStorage.getItem("accessToken")}`
            },
            credentials: "include"
        });

        const rows = await res.json();

        if (rows.length < 50) hasMore.set(false);

        messages.update(m => [...rows, ...m]);

    } catch (err) {
        console.error(" loadMessages error:", err);
    }
}
