<script>
  import { onMount } from 'svelte';
  import { createGrid, ModuleRegistry, AllCommunityModule } from 'ag-grid-community';
  import 'ag-grid-community/styles/ag-theme-alpine.css';
  import 'ag-grid-community/styles/ag-theme-quartz.css';
  import 'ag-grid-community/styles/ag-theme-balham.css';
  import 'ag-grid-community/styles/ag-theme-material.css';

  // NG: dng AllCommunityModule (s t)
  ModuleRegistry.registerModules([AllCommunityModule]);

  let gridDiv;
  let gridApi;

  let currentTheme = 'ag-theme-alpine';

  const themes = [
    { name: 'Alpine', class: 'ag-theme-alpine' },
    { name: 'Quartz', class: 'ag-theme-quartz' },
    { name: 'Balham', class: 'ag-theme-balham' },
    { name: 'Material', class: 'ag-theme-material' }
  ];

  let columnDefs = [
    { field: 'athlete', headerName: 'VV', sortable: true, filter: true, flex: 2 },
    { field: 'age', headerName: 'Tui', width: 100 },
    { field: 'country', headerName: 'Quc gia', sortable: true, filter: true, flex: 2 },
    { field: 'year', headerName: 'Nm', width: 100 },
    { field: 'date', headerName: 'Ngy', width: 130 },
    { field: 'sport', headerName: 'Mn thi', sortable: true, filter: true, flex: 2 },
    { field: 'gold', headerName: 'Vng', width: 100, cellStyle: { color: '#d4a017' } },
    { field: 'silver', headerName: 'Bc', width: 100, cellStyle: { color: '#a8a8a8' } },
    { field: 'bronze', headerName: 'ng', width: 100, cellStyle: { color: '#cd7f32' } },
    { field: 'total', headerName: 'Tng', width: 110, cellStyle: { fontWeight: 'bold' } }
  ];

  let rowData = [];

  onMount(async () => {
    try {
      const res = await fetch('https://www.ag-grid.com/example-assets/olympic-winners.json');
      if (!res.ok) throw new Error('Khng ti c d liu');
      rowData = await res.json();
      console.log(` ti ${rowData.length} dng d liu thnh cng!`);

      const gridOptions = {
        columnDefs,
        rowData,
        pagination: true,
        paginationPageSize: 20,
        paginationPageSizeSelector: [10, 20, 50, 100],
        animateRows: true,
        defaultColDef: {
          flex: 1,
          minWidth: 100,
          resizable: true,
          sortable: true,
          filter: true
        },
        overlayLoadingTemplate: '<span class="ag-overlay-loading-center">ang ti d liu...</span>',
        overlayNoRowsTemplate: '<span class="ag-overlay-loading-center">Khng c d liu</span>',

        onGridReady: (params) => {
          gridApi = params.api;
        },

        onFirstDataRendered: () => {
          if (gridDiv && gridDiv.offsetWidth > 0) {
            gridApi.sizeColumnsToFit();
          }
        },

        onGridSizeChanged: () => {
          if (gridDiv && gridDiv.offsetWidth > 0) {
            gridApi.sizeColumnsToFit();
          }
        }
      };

      createGrid(gridDiv, gridOptions);
    } catch (err) {
      console.error('Li khi ti d liu:', err);
      rowData = [{ athlete: 'Li', country: 'Kim tra mng' }];
    }
  });

  function changeTheme(themeClass) {
    if (!gridDiv) return;

    themes.forEach((t) => gridDiv.classList.remove(t.class));
    gridDiv.classList.add(themeClass);
    currentTheme = themeClass;

    if (gridApi && gridDiv.offsetWidth > 0) {
      setTimeout(() => gridApi.sizeColumnsToFit(), 0);
    }
  }

  function refreshGrid() {
    if (gridApi) {
      gridApi.refreshCells({ force: true });
    }
  }
</script>

<!-- Toolbar -->
<div class="toolbar">
  <div class="toolbar-group">
    <span class="label">Theme:</span>
    <select bind:value={currentTheme} on:change={(e) => changeTheme(e.target.value)}>
      {#each themes as theme}
        <option value={theme.class}>{theme.name}</option>
      {/each}
    </select>
  </div>

  <div class="toolbar-group">
    <button on:click={refreshGrid} class="refresh-btn">Lm mi Grid</button>
  </div>
</div>

<!-- Grid Container -->
<div class="grid-wrapper">
  <div class="ag-theme-alpine" style="height: 100%; width: 100%;" bind:this={gridDiv}></div>
</div>

