<script>
  import { onMount, onDestroy } from 'svelte';
  import { user, accessToken, loading } from '$lib/stores';
  import { getRefreshToken, saveRefreshToken, clearRefreshToken } from '$lib/db/indexeddb';

  import Home from '@routes/Home.svelte';
  import Login from '@routes/Login.svelte';
  import Loading from '@components/loading/index.svelte';
  import { connectWebSocket } from '$lib/websocket-client.js';
  import { initTheme } from '$lib/theme';

  import ApiClient from '$lib/api';
  const api = new ApiClient();

  let hasWarned = false;

  // ========== AUTH CHECK ==========
  const checkAuth = async () => {
    loading.set(true);
    const storedRefresh = await getRefreshToken();
    console.log('Stored refresh token:', storedRefresh);
    try {
      let token = $accessToken;

      // Khng c accessToken  dng refreshToken
      if (!token) {
        const refreshToken = await getRefreshToken();
        if (refreshToken) {
          const res = await api.post('/auth/refresh', { refreshToken });
          token = res.accessToken;
          accessToken.set(token);

          if (res.refreshToken) {
            await saveRefreshToken(res.refreshToken);
          }
        }
      }

      // Nu c accessToken  gi /auth/me xc thc user
      if (token) {
        const res = await api.get('/auth/me', {
          headers: { Authorization: `Bearer ${token}` }
        });
        user.set(res.user);
      } else {
        user.set(null);
      }
    } catch (err) {
      console.warn('Auto refresh failed:', err);
      await clearRefreshToken();
      accessToken.set('');
      user.set(null);
    } finally {
      loading.set(false);
    }
  };

  // ========== LIFECYCLE ==========
  onMount(async () => {
    if (!window.crypto?.subtle) {
      console.error('Web Crypto API not supported');
    }

    initTheme();
    connectWebSocket();

    window.addEventListener('beforeunload', handleBeforeUnload);
    window.addEventListener('popstate', handleGoBack);

    await checkAuth();
  });

  onDestroy(() => {
    window.removeEventListener('beforeunload', handleBeforeUnload);
    window.removeEventListener('popstate', handleGoBack);
  });

  // ========== WARN BEFORE EXIT ==========
  function handleBeforeUnload(e) {
    if ($user) {
      e.preventDefault();
      e.returnValue = '';
    }
  }

  function handleGoBack() {
    if ($user && !hasWarned) {
      const confirmLeave = confirm('Bn c chc mun ri khi ng dng khng?');
      if (!confirmLeave) {
        history.pushState(null, '', location.href);
      } else {
        hasWarned = true;
        history.back();
      }
    }
  }
</script>

{#if $loading}
  <Loading />
{:else if $user}
  <Home />
{:else}
  <Login />
{/if}
