<script>
  export let images = [];
  export let index = 0;
  export let onClose = () => {};

  let scale = 1;
  let translateX = 0;
  let translateY = 0;
  let isDragging = false;
  let startX, startY;

  let touchStartX = 0;
  let touchStartY = 0;
  let pinchStartScale = 1;
  let pinchDistance = 0;

  // inertia swipe close threshold
  const closeSpeed = 130;
  let lastMoveTime = 0;
  let lastMoveY = 0;

  // Metadata
  let meta = { width: 0, height: 0, size: null };

  // Fetch EXIF friendly (fallback)
  function loadMetadata(url) {
    const img = new Image();
    img.onload = () => {
      meta.width = img.width;
      meta.height = img.height;
    };
    img.src = url;

    fetch(url, { method: 'HEAD' })
      .then((res) => {
        const size = res.headers.get('content-length');
        if (size) meta.size = formatBytes(parseInt(size));
      })
      .catch(() => {});
  }

  $: loadMetadata(images[index]);

  function formatBytes(bytes) {
    const sizes = ['B', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(1024));
    return `${(bytes / Math.pow(1024, i)).toFixed(1)} ${sizes[i]}`;
  }

  /** Animations */
  let animateClass = 'zoom-in';
  let thumbContainer;

  $: scrollThumbnailIntoView();

  function scrollThumbnailIntoView() {
    if (!thumbContainer) return;
    const selectedThumb = thumbContainer.querySelector(`.thumb-${index}`);
    if (selectedThumb) {
      selectedThumb.scrollIntoView({ behavior: 'smooth', inline: 'center', block: 'nearest' });
    }
  }

  /** Slide change */
  function next() {
    if (index < images.length - 1) {
      animateSlide('left');
      index++;
      resetTransform();
    }
  }
  function prev() {
    if (index > 0) {
      animateSlide('right');
      index--;
      resetTransform();
    }
  }
  function animateSlide(dir) {
    animateClass = dir === 'left' ? 'slide-left' : 'slide-right';
    setTimeout(() => (animateClass = ''), 250);
  }

  /** Zoom */
  function wheelZoom(e) {
    scale += e.deltaY * -0.002;
    scale = Math.min(Math.max(scale, 1), 4);
  }

  function toggleZoom(e) {
    if (scale < 2) scale = 2;
    else if (scale < 4) scale = 4;
    else resetTransform();

    const rect = e.target.getBoundingClientRect();
    translateX = (rect.width / 2 - (e.clientX - rect.left)) * (scale - 1);
    translateY = (rect.height / 2 - (e.clientY - rect.top)) * (scale - 1);
  }

  /** Drag & inertia close */
  function dragStart(e) {
    const t = e.touches ? e.touches[0] : e;
    if (scale === 1) {
      lastMoveTime = performance.now();
      lastMoveY = t.clientY;
    }

    isDragging = true;
    startX = t.clientX - translateX;
    startY = t.clientY - translateY;
  }

  function dragMove(e) {
    if (!isDragging) return;
    const t = e.touches ? e.touches[0] : e;

    translateX = t.clientX - startX;
    translateY = t.clientY - startY;

    if (scale === 1) {
      const now = performance.now();
      const dt = now - lastMoveTime;
      const dy = t.clientY - lastMoveY;

      if (dt > 0 && Math.abs((dy / dt) * 1000) > closeSpeed) {
        animateClass = 'swipe-close';
        setTimeout(onClose, 150);
      }

      lastMoveTime = now;
      lastMoveY = t.clientY;
    }
  }

  function dragEnd() {
    isDragging = false;
  }

  /** Swipe (change image) */
  function swipeStart(e) {
    const t = e.touches ? e.touches[0] : e;
    touchStartX = t.clientX;
    touchStartY = t.clientY;
  }

  function swipeEnd(e) {
    const t = e.changedTouches ? e.changedTouches[0] : e;
    const diffX = t.clientX - touchStartX;

    if (scale === 1 && Math.abs(diffX) > 60) diffX < 0 ? next() : prev();
  }

  /** Pinch zoom */
  function pinchStart(e) {
    if (e.touches.length < 2) return;
    pinchDistance = getDist(e.touches);
    pinchStartScale = scale;
  }
  function pinchMove(e) {
    if (e.touches.length < 2) return;
    const newDist = getDist(e.touches);
    scale = Math.min(Math.max(pinchStartScale * (newDist / pinchDistance), 1), 4);
  }
  function getDist(t) {
    return Math.hypot(t[0].clientX - t[1].clientX, t[0].clientY - t[1].clientY);
  }

  function resetTransform() {
    scale = 1;
    translateX = translateY = 0;
  }

  /** Share / Download / Open */
  function shareNow() {
    if (navigator.share) navigator.share({ url: images[index] });
    else window.open(images[index], '_blank');
  }
  function downloadNow() {
    const link = document.createElement('a');
    link.href = images[index];
    link.download = `photo-${index + 1}`;
    link.click();
  }
</script>

<div class="overlay" on:click={() => onClose()}>
  <div
    class="viewer"
    on:click|stopPropagation
    on:wheel={wheelZoom}
    on:mousedown={dragStart}
    on:mousemove={dragMove}
    on:mouseup={dragEnd}
    on:mouseleave={dragEnd}
    on:touchstart={(e) => {
      swipeStart(e);
      pinchStart(e);
    }}
    on:touchmove={(e) => pinchMove(e)}
    on:touchend={swipeEnd}>
    <img
      src={images[index]}
      class="main-img {animateClass}"
      style="transform: translate({translateX}px, {translateY}px) scale({scale});"
      on:dblclick={toggleZoom}
      draggable="false" />

    <!-- Close -->
    <button class="btn close" on:click={() => onClose()}></button>

    <!-- Prev / Next -->
    {#if index > 0}
      <button class="btn nav left" on:click={prev}></button>
    {/if}
    {#if index < images.length - 1}
      <button class="btn nav right" on:click={next}></button>
    {/if}

    <!-- Toolbox -->
    <div class="toolbox">
      <button on:click={shareNow}> Share</button>
      <button on:click={downloadNow}> Download</button>
      <button on:click={() => window.open(images[index], '_blank')}> Open</button>
    </div>

    <!-- Metadata -->
    <div class="meta">
      <strong> Thng tin</strong>
      <br />
      Resolution: {meta.width}{meta.height}
      <br />
      {#if meta.size}Size: {meta.size}{/if}
    </div>

    <!-- Counter -->
    <div class="counter">{index + 1} / {images.length}</div>

    <!-- Thumbnails -->
    <div bind:this={thumbContainer} class="thumb-wrap">
      {#each images as img, i}
        <img
          src={img}
          class="thumb thumb-{i} {index === i ? 'active' : ''}"
          on:click={() => {
            index = i;
            resetTransform();
          }} />
      {/each}
    </div>
  </div>
</div>

<style>
  /* ===== ZALO STYLE GALLERY VIEWER ===== */

  .overlay {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.82); /* dark but not too strong */
    backdrop-filter: blur(4px);
    animation: fade 0.18s ease-out;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @keyframes fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .viewer {
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  /* MAIN IMG */
  /* .main-img {
    max-width: 94vw;
    max-height: 92vh;
    object-fit: contain;
    border-radius: 14px;
    box-shadow: 0 4px 38px rgba(0, 0, 0, 0.55);
    transition: transform 0.18s ease-out;
    pointer-events: none;
    user-select: none;
  } */

  .main-img {
    max-width: 95vw;
    max-height: 95vh;
    object-fit: contain;
    image-rendering: auto;
    image-rendering: smooth;
  }

  /* Zalo style glass button */
  .btn {
    position: fixed;
    background: rgba(255, 255, 255, 0.13);
    border: none;
    color: white;
    backdrop-filter: blur(10px);
    border-radius: 99px;
    padding: 10px 14px;
    font-size: 22px;
    transition: all 0.18s ease;
    cursor: pointer;
  }

  .btn:hover {
    background: rgba(255, 255, 255, 0.25);
    transform: scale(1.08);
  }

  /* Close */
  .close {
    top: 16px;
    right: 18px;
    font-size: 22px;
    padding: 8px 13px;
  }

  /* Slides */
  .nav {
    top: 50%;
    transform: translateY(-50%);
    font-size: 26px;
    padding: 10px 16px;
  }
  .nav.left {
    left: 18px;
  }
  .nav.right {
    right: 18px;
  }

  /* Toolbar top  Share / Download / Open */
  .toolbox {
    position: fixed;
    top: 18px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 8px;
    z-index: 9999;
  }

  .toolbox button {
    background: rgba(255, 255, 255, 0.14);
    backdrop-filter: blur(12px);
    border: none;
    color: white;
    padding: 5px 13px;
    border-radius: 20px;
    font-size: 13px;
    cursor: pointer;
    white-space: nowrap;
    transition: 0.18s ease;
  }

  .toolbox button:hover {
    background: rgba(255, 255, 255, 0.25);
  }

  /* Info box (top-left mini) */
  .meta {
    background: rgba(255, 255, 255, 0.14);
    backdrop-filter: blur(12px);
    padding: 7px 12px;
    border-radius: 10px;
    color: white;
    font-size: 12px;
    line-height: 1.2rem;

    position: fixed;
    top: 70px;
    left: 18px;
  }

  /* Photo counter */
  .counter {
    position: fixed;
    top: 70px;
    right: 18px;
    background: rgba(255, 255, 255, 0.14);
    backdrop-filter: blur(12px);
    padding: 5px 12px;
    border-radius: 16px;
    color: white;
    font-size: 12px;
  }

  /* Thumbnail list */
  .thumb-wrap {
    position: fixed;
    bottom: 14px;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100vw - 20px);
    display: flex;
    gap: 7px;
    overflow-x: auto;
    padding-bottom: 3px;
    justify-content: center;
  }

  /* Single thumbnail */
  .thumb {
    width: 60px;
    height: 44px;
    object-fit: cover;
    border-radius: 10px;
    opacity: 0.55;
    cursor: pointer;
    transition: 0.18s ease;
    border: 2px solid transparent;
  }

  .thumb:hover {
    opacity: 0.9;
    transform: scale(1.04);
  }

  .thumb.active {
    opacity: 1;
    border: 2px solid #fff;
    transform: scale(1.08);
  }

  /* Animation when switching */
  .slide-left {
    animation: slideL 0.25s ease-out;
  }
  .slide-right {
    animation: slideR 0.25s ease-out;
  }
  .swipe-close {
    animation: swipeFade 0.15s ease-in forwards;
  }

  @keyframes slideL {
    from {
      transform: translateX(60px);
      opacity: 0.6;
    }
  }
  @keyframes slideR {
    from {
      transform: translateX(-60px);
      opacity: 0.6;
    }
  }
  @keyframes swipeFade {
    to {
      opacity: 0;
      transform: scale(0.7);
    }
  }
</style>
