<script>
  import { onMount } from 'svelte';
  import { connectWebSocket, sendMessage, users, status, myName, messages, myId } from '$lib/websocket-client.js';
  import Message from '@components/Message.svelte';

  import { mgss, meId } from '$lib/messages-sample.js';

  let input = '';
  let selected = 'all';
  let chatBox;

  onMount(() => {
    // connectWebSocket();
  });

  // Map + Rename + Timestamp
  $: processedUsers = $users.map((u) => {
    const lastMsg = [...$messages]
      .filter((m) => m.from == u.id || m.to == u.id)
      .sort((a, b) => new Date(b.created_at || b.time || 0) - new Date(a.created_at || a.time || 0))[0];

    return {
      ...u,
      name: u.id === $myId ? 'MyCloud' : u.name,
      lastMessageAt: lastMsg ? new Date(lastMsg.created_at || lastMsg.time) : null
    };
  });

  // Sort list
  $: sortedUsers = processedUsers
    .sort((a, b) => {
      if (a.id === $myId) return -1;
      if (b.id === $myId) return 1;
      if (a.lastMessageAt && b.lastMessageAt) return b.lastMessageAt - a.lastMessageAt;
      if (a.lastMessageAt) return -1;
      if (b.lastMessageAt) return 1;
      return 0;
    })
    .filter((u) => u.id !== null);

  function send() {
    if (input.trim()) {
      sendMessage(selected, input);
      input = '';
    }
  }
</script>

<!-- Dark Mode Wrapper -->
<main
  class="flex h-screen transition-colors duration-300
             bg-gray-100 text-gray-800
             dark:bg-gray-900 dark:text-gray-100">
  <!-- SIDEBAR -->
  <aside
    class="w-52 border-r p-3 flex flex-col gap-2 shadow-sm
                bg-white dark:bg-gray-800 dark:border-gray-700">
    <!-- Header -->
    <p class="text-sm font-medium mb-1 flex items-center gap-1">
      <span class="font-semibold text-blue-600 dark:text-blue-400 truncate">{$myName}</span>

      <span
        class="text-[10px] px-2 py-[1px] rounded-full
               {$status.includes('Connected')
          ? 'bg-green-100 text-green-600 dark:bg-green-900 dark:text-green-300'
          : 'bg-red-100 text-red-600 dark:bg-red-900 dark:text-red-300'}">
        {$status.replace(/\s.*$/, '')}
      </span>
    </p>

    <!-- User List -->
    {#each sortedUsers as u}
      <div
        on:click={() => (selected = u.id)}
        class="p-2 rounded-md mb-1 cursor-pointer transition select-none
              border border-transparent
              hover:bg-blue-50 dark:hover:bg-gray-700
              active:scale-[0.98]
              {selected === u.id ? 'bg-blue-100 dark:bg-blue-900 border-blue-300 dark:border-blue-600 shadow-sm' : ''}">
        <span class="font-medium text-gray-700 dark:text-gray-200">{u.name}</span>

        {#if u.lastMessageAt}
          <span class="text-[11px] text-gray-400 dark:text-gray-500 block mt-[1px] leading-tight">
            {new Date(u.lastMessageAt).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
          </span>
        {/if}
      </div>
    {/each}
  </aside>

  <!-- CHAT AREA -->
  <div class="flex-1 flex flex-col">
    <div
      bind:this={chatBox}
      class="flex-1 border-b p-3 overflow-y-auto
                border-gray-200 dark:border-gray-700">
      {#each mgss as msg (msg.id)}
        <Message {msg} myId={meId} />
      {/each}
    </div>

    <!-- Input -->
    <form
      on:submit|preventDefault={send}
      class="flex p-3 gap-2 bg-white dark:bg-gray-800 border-t dark:border-gray-700">
      <input
        bind:value={input}
        placeholder="Type message..."
        class="flex-grow rounded px-3 py-2 border
               border-gray-300 dark:border-gray-600
               bg-white dark:bg-gray-700
               text-gray-800 dark:text-gray-100
               placeholder-gray-400 dark:placeholder-gray-500
               focus:outline-none focus:ring-2 focus:ring-blue-400 dark:focus:ring-blue-600" />

      <button
        type="submit"
        class="px-4 py-2 rounded text-white bg-blue-500 hover:bg-blue-600 dark:bg-blue-700 dark:hover:bg-blue-600 transition">
        Send
      </button>
    </form>
  </div>
</main>
